@import url(https://fonts.googleapis.com/css2?family=Tajawal:wght@200;300;400;500;700;800&display=swap);
:root{--profile-size: 120px;--description-size: 620px;--profile-width: 500px;--professional-blue: #2478ff;--background-white: #ffffff}.flex-box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}body{color:#345;margin:0;padding:0;padding-bottom:10em;font-family:"Poppins", sans-serif;font-weight:200;background-color:#ffffff;background-color:var(--background-white)}h2{color:#345}h3{color:#345}h4{color:#345;font-weight:400;font-size:1em}a{color:#7a7c80;text-decoration:none}.section-1{width:500px;width:var(--profile-width);padding:5em;padding-bottom:0;text-align:center}.section-1 p{font-size:1rem;padding-bottom:10px;margin:0}.section-1 h2{color:#2478ff;color:var(--professional-blue);font-weight:500;font-size:2em;margin:5px}.section-1 h3{margin:5px;font-size:17px;font-weight:400}.section-1 strong{font-weight:600}.section-1 a{font-size:1.5rem;padding:10px}.section-2{padding-top:10vh;width:620px;width:var(--description-size)}.section-2 .quote{padding:1em 0;font-size:23px;display:-webkit-flex;display:flex;font-weight:500;font-style:italic;-webkit-align-items:center;align-items:center}.section-2 .quote div{border-left:6px solid #2478ff;border-left:6px solid var(--professional-blue);padding:0.8em 1em;background-color:#f6f6f6}.section-2 strong{font-weight:600}.section-2 h3{font-size:1.5rem;font-weight:400;margin-bottom:10px;margin-top:20px}.section-2 p{font-size:1rem;font-weight:300;padding-bottom:10px;margin:0}.section-2 ul{margin:0px;line-height:180%;font-size:0.9rem;font-weight:300;margin-bottom:5px}.section-2 a{display:block;padding:5px;font-size:1.2rem;padding-left:0;width:100px}.section-1 a:hover{color:#fff;cursor:pointer;transition:0.3s}.white:hover{position:relative;padding-left:10px}@media (max-width: 1000px){.grid-2{grid-template-columns:1fr}.section-1{padding:0;padding-top:5rem}.section-2{padding:1em;padding-left:1.5rem;padding-top:2rem}.quote{-webkit-justify-content:center;justify-content:center}.quote div{text-align:center;width:100%}}.circular--landscape{display:inline-block;position:relative;width:120px;width:var(--profile-size);height:120px;height:var(--profile-size);overflow:hidden;border-radius:50%;background-image:url(../../static/media/profile.b2a27d4c.jpg);background-repeat:no-repeat;background-position:center center;background-size:calc(120px * 1.1);background-size:calc(var(--profile-size) * 1.1)}.search-container{margin:1em 0em}.tags{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin:-0.3em}.tags .tag{padding:0.3em}.tags .tag .toggle-tag{display:-webkit-flex;display:flex;font-size:0.8em;font-weight:300;border-radius:3px;overflow:hidden;position:relative}.tags .tag .toggle-tag button{border:none;display:block;font-size:0.9em;font-weight:300;text-align:center;background-color:#f4f6f8;color:#345;padding:0.3em 0.7em;width:100%;white-space:nowrap;text-decoration:none}.tags .tag .toggle-tag:hover{text-decoration:none;-webkit-filter:brightness(0.95);filter:brightness(0.95);cursor:pointer}.projects .project{margin:3em 0;margin-bottom:4em}.projects .project .links{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.projects .project .links div{padding:1em}.projects .project .links div a{min-width:11em;width:100%;letter-spacing:0.05rem;font-weight:400;margin-left:-1.5em;font-size:0.8em;padding:0.5em;color:white;border-radius:4px;background-color:#2478ff;background-color:var(--professional-blue);text-align:center}


